@charset "UTF-8";
@import "https://fonts.googleapis.com/css?family=PT+Sans:400,400i,700,700i&subset=cyrillic";
@import url(../fonts/LucidaGrande/styles.css);
@import url(../fonts/LucidaGrandeBold/styles.css);
@import url(../fonts/PlumbCondensed/styles.css);
